@media (max-width: 1150px){
	.header__nav-list--mobile{
		list-style: none;
		margin: 0;
		padding: 0;
		width: 300px;
		background-color: #ffffff;
		position: fixed;
		height: 100%;
		top: 0;
		right: 0;
		padding-top: 50px;
		box-sizing: border-box;
		padding-left: 50px;
		padding-right: 50px;
		z-index: 200000;
		pointer-events: none;
		opacity: 0;
		transform: translateX(2000px);
		transition: all 1s ease;
	}
	.header__nav-list--mobile-show{
		transform: translateX(0);
		pointer-events: auto;
		opacity: 1;
	}
	.header__nav-item{
		padding-top: 30px;
		padding-bottom: 30px;
		border-bottom: 1px solid rgba(29, 29, 29, 0.2);
		transition: all 0.4s ease;
	}
	.header__nav-link{
		text-decoration: none;
		text-transform: uppercase;
		color: #1d1d1d;
		font-size: 1em;
	}
	.header__nav-item:hover{
		border-bottom: 1px solid rgba(29, 29, 29, 1);
	}
	.header__btn--mobile{
		background: transparent;
		border: none;
		background-color: rgba(29, 29, 29, 1);
		width: 55px;
		height: 55px;
		position: absolute;
		top: 0;
		right: 0;
	}
	.header__btn--mobile::before,
	.header__btn--mobile::after{
		content: '';
		position: absolute;
		width: 1px;
		height: 35px;
		background-color: #ffffff;
		top: 50%;
		transform: translateY(-50%) rotate(45deg);
	}
	.header__btn--mobile::after{
		transform: translateY(-50%) rotate(-45deg);
	}

}
	
